
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
Group,
Cell
},
computed: {
myInfo () {
return this.$store.state.myInfo
}
},
methods: {
logout () {
let that = this
this.$vux.confirm.show({
title: '确定要注销帐号？',
onConfirm () {
that.$store.dispatch('logout')
}
})
}
}
}



